section#basic--main {
    position: relative;
    padding-top: 100vh;
    @media (min-width: 768px) {
        padding-top: 400px;
    }
    @media (min-width: 1680px) {
        padding-top: 723.83px;
    }

    figure.intro--image-wrap {
        position: fixed;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100vh;
        overflow: hidden;

        @media (min-width: 768px) {
            height: 400px;
        }
        @media (min-width: 1680px) {
            height: 723.83px;
        }

        img {
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }
    }
}
