$mobileContainerHeightShared: 60px;

header#root-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    width: 100%;
    min-height: $mobileContainerHeightShared;
    // background-color: rgba(226, 231, 225, 1);

    .mobile {
        position: relative;
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        min-height: $mobileContainerHeightShared;
        // border: 1px solid deepskyblue;

        .logo,
        .cart--icon,
        .hamburger,
        .login--button,
        .search--input-desk {
            @include centeredY();
        }

        .logo {
            left: 1rem;
            width: 30px;

            @media (min-width: 768px) {
                width: 35px;
            }

            @media (min-width: 1680px) {
                left: 0;
            }
        }

        .cart--icon {
            left: 3.8rem;
            width: 24px;

            @media (min-width: 768px) {
                left: initial;
                right: 1rem;
            }

            @media (min-width: 1680px) {
                right: 0;
            }

            figcaption {
                position: absolute;
                top: -5px;
                right: -5px;
                width: 14px;
                height: 14px;
                background-color: lightseagreen;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 11px;
                color: white;
            }
        }

        .login--button {
            left: 4rem;
            font-weight: lighter;
            display: none;

            @media (min-width: 768px) {
                display: block;
            }
        }

        form {
            display: none;

            @media (min-width: 768px) {
                display: block;
            }

            .search--input-desk {
                right: 3rem;

                input[type='text'] {
                    width: 250px;
                    height: 1.7rem;
                    background-color: rgba(255, 255, 255, 0.3);
                    border: 0;
                    border-radius: 10px;
                    padding-left: 0.5rem;
                    padding-right: 1.7rem;
                    color: rgba(0, 0, 0, 0.3);

                    &:focus {
                        color: rgba(0, 0, 0, 0.88);
                    }

                    &:focus + input[type='submit'] {
                        opacity: 1;
                    }
                }

                input[type='submit'] {
                    @include centeredY();
                    right: 0.5rem;
                    width: 16px;
                    opacity: 0.3;
                    border: 0;
                    background-color: rgba(0, 0, 0, 0);
                    background-image: url('/assets/images/icons/search.svg');
                    background-size: 16px;
                    background-repeat: no-repeat;
                    background-position: center;
                    cursor: pointer;

                    &:hover {
                        opacity: 1;
                    }
                }
            }
        }

        .hamburger {
            right: 1rem;

            @media (min-width: 768px) {
                display: none;
            }
        }

        nav.mobile--nav-container {
            position: absolute;
            top: 0;
            right: -100%;
            width: 100%;
            height: 100vh;
            overflow: hidden;
            // background-color: rgba(0, 0, 0, 0.4);

            @media (min-width: 768px) {
                display: none;
            }

            .mobile--nav-wrap {
                position: absolute;
                top: 0;
                right: 0;
                width: 83.5%;
                height: 100%;
                padding: 0 1.5rem;
                overflow-y: scroll;
                overscroll-behavior-y: contain;
                background-color: white;

                figure {
                    height: 60px;
                    display: flex;
                    align-items: center;

                    img.close {
                        width: 28px;
                        margin-left: -5px;
                        cursor: pointer;
                    }
                }

                form {
                    @media (max-width: 767px) {
                        display: block;
                    }

                    label.search--input-mobile {
                        position: relative;

                        input[type='text'] {
                            width: 100%;
                            height: 1.7rem;
                            border: 0;
                            border-bottom: 1px solid rgba(0, 0, 0, 0.5);
                            padding-left: 0.5rem;
                            padding-right: 1.7rem;
                            margin: 2rem 0;
                            color: rgba(0, 0, 0, 0.3);

                            &:focus {
                                color: rgba(0, 0, 0, 0.88);
                            }

                            &:focus + input[type='submit'] {
                                opacity: 1;
                            }
                        }

                        input[type='submit'] {
                            @include centeredY();
                            right: 0.5rem;
                            width: 16px;
                            opacity: 0.3;
                            border: 0;
                            background-color: rgba(0, 0, 0, 0);
                            background-image: url('/assets/images/icons/search.svg');
                            background-size: 16px;
                            background-repeat: no-repeat;
                            background-position: center;
                            cursor: pointer;

                            &:hover {
                                opacity: 1;
                            }
                        }
                    }
                }

                .sign {
                    text-align: center;
                    font-size: 11px;
                    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
                    padding: 1rem 0;
                    margin-bottom: 1rem;
                }

                ul.menu--wrap {
                    display: flex;
                    flex-wrap: wrap;

                    li {
                        position: relative;
                        width: 50%;
                        height: 120px;

                        figure {
                            @include centered();
                            top: 40%;
                            width: 55%;
                            height: 55%;
                            display: flex;
                            flex-direction: column;
                            align-items: center;

                            img {
                                height: 100%;
                            }

                            figcaption {
                                font-size: 12px;
                                white-space: nowrap;
                            }
                        }

                        div {
                            position: absolute;
                        }

                        &:first-child,
                        &:nth-child(3),
                        &:nth-child(5) {
                            figure {
                                left: 45%;
                            }

                            div {
                                &.line--one {
                                    bottom: 0;
                                    width: 90%;
                                    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                                }

                                &.line--two {
                                    right: 0;
                                    height: 90%;
                                    border-left: 1px solid rgba(0, 0, 0, 0.1);
                                }
                            }
                        }

                        &:nth-child(3),
                        &:nth-child(5) {
                            div {
                                &.line--two {
                                    height: 80%;
                                    top: 50%;
                                    transform: translateY(-50%);
                                }
                            }
                        }

                        &:nth-child(5),
                        &:nth-child(6) {
                            div {
                                &.line--one {
                                    display: none;
                                }
                            }
                        }

                        &:nth-child(2),
                        &:nth-child(4),
                        &:nth-child(6) {
                            figure {
                                left: 55%;
                            }
                            div {
                                &.line--one {
                                    bottom: 0;
                                    right: 0;
                                    width: 90%;
                                    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
